
import { Location, Setting } from "@element-plus/icons";
import { reactive, toRefs } from "vue";
export default {
  name: "SideMenu",
  components: {
    Location,
    Setting,
  },
  props: {
    isCollapse: Boolean,
  },
  setup(props, context) {
    const state = reactive({
      menuItem: [
        {
          label: "코로나 현황 요약",
          index: "corona-briefing",
          icon: "location",
          submenus: [
            {
              label: "국가별 요약",
              index: "InternationalCases",
              path: "corona",
            },
            { label: "국내 요약", index: "DomesticCases", path: "corona" },
          ],
        },
        {
          label: "코로나 분석",
          index: "corona-analysis",
          icon: "Setting",
          submenus: [
            {
              label: "샘플메뉴1",
              index: "sample1",
              path: "corona",
            },
            { label: "샘플메뉴2", index: "sample2", path: "corona" },
          ],
        },
      ],
    });
    const handleSelect = (item) => {
      context.emit("menuSelect", item);
    };
    return {
      ...toRefs(state),
      handleSelect,
    };
  },
};
